import React, { Component } from 'react';
import { ComponentLeader, Loader } from '../widgets';
import Particles from 'react-particles-js';
import AOS from 'aos';

import{ YeahModal,SalesModal } from '../widgets/modals';
const features = require('./data').default;
require('../../styles/aos/aos.scss');

class ShortTermIndexPage extends Component {

    constructor(props) {
        super(props);

        this.state = {
            showModal: false
        }
        AOS.init();
    }

    componentDidMount() {
        (function(){
		'use strict';

		var items = document.querySelectorAll(".timeline li");

		function isElementInView(el){
			var rect = el.getBoundingClientRect();
			return(
			rect.top >= 0&&
			rect.left >= 0 &&
			rect.bottom <=(window.innerHeight||document.documentElement.clientHeight)&&
			rect.right <=(window.innerWidth||document.documentElement.clientWidth)
			);
		}

		function callbackFunc(){
			for(var i=0;i<items.length;i++)
			{
				if (isElementInView(items[i])){
					items[i].classList.add("in-view");
				}
			}
		}

		window.addEventListener("load",callbackFunc);
		window.addEventListener("resize",callbackFunc);
		window.addEventListener("scroll",callbackFunc);

	})();
    }

    openSalesModal(){
        this.setState({
            showSalesModal: true
        })
    }

    render() {
        return (
            <div>
                <ComponentLeader title="Short-Term Service"/>
                <div className="short-term">
                    <div className="container-fluid">
                        {this.renderTimeline()}
                        <div className="features">
                            <div className="row">
                                {this.renderFeatures()}                      
                            </div>
                        </div>
                        {this.renderTable()}
                        {this.renderEnrollNow()}
                        {this.state.showSalesModal && <YeahModal><SalesModal /></YeahModal>}
                    </div>
                </div>
            </div>
            
        )
    }

    renderTimeline() {
        return (
            <div className="timeline">
                <ul>
                    <h1>Service Process</h1>
                    <li><div>
                        <h2>8</h2>
                        <h3></h3>
                        <h5> </h5>
                        <h5> PS</h5>
                    </div></li>
                    <li><div>
                        <h2>9</h2>
                        <h3>PS</h3>
                        <h5> </h5>
                        <h5> PS</h5>
                        <h5> PS</h5>            
                    </div></li>

                    <li><div>
                        <h2>10</h2>
                        <h3>PS</h3>
                        <h5> PS</h5>
                        <h5> </h5>
                    </div></li>	

                    <li><div>
                        <h2>11</h2>
                        <h3>PS</h3>
                        <h5> </h5>
                        <h5> </h5>
                    </div></li>

                    <li><div>
                        <h1></h1>
                    </div></li>	
                    <br />
            </ul>
            </div>
        );
    }

    renderFeatures() {
        if (features) {
            return features.map((feature, index) => {
                return (
                    <div className="col-xs-6 col-md-4" key={index} data-aos="fade-up" id="feature-on">
                        <div id="image">
                            <img src={feature.link}/>                                    
                        </div>
                        <span>
                            <h3>{feature.title}</h3>
                            <p>{feature.description}</p>
                        </span>
                    </div>
                );
            });
        } else {
           return <Loader />
        }
    }

    renderTable() {
        return (
            <div className="table container">
                <div className="row">
                    <div className=".col-xs-6 .col-sm-3">
                        <div className="table-cell"></div>
                        <span>
                            <div className="table-cell">
                                <h5>Target School</h5>
                            </div>
                            <div className="table-cell">
                                <h5></h5>
                            </div>
                            <div className="table-cell">
                                <h5></h5>
                            </div>
                            <div className="table-cell">
                                <h5></h5>
                            </div>
                            <div className="table-cell">
                                <h5>11</h5>
                            </div>
                            <div className="table-cell">
                                <h5></h5>
                            </div>
                            <div className="table-cell">
                                <h5></h5>
                            </div>
                        </span>                        
                    </div>
                    <div className=".col-xs-6 .col-sm-3">
                        <div className="table-cell service-name-1"></div>
                        <span>
                            <div className="table-cell">
                                <h5 className="service-school">UC</h5>
                            </div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell unchecked"></div>
                            <div className="table-cell unchecked"></div>
                            <div className="table-cell unchecked"></div>
                        </span>                        
                    </div>
                    <div className=".col-xs-6 .col-sm-3">
                        <div className="table-cell service-name-2"></div>
                        <span>
                            <div className="table-cell">
                                <h5 className="service-school">UCB/UCLA</h5>
                            </div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell unchecked"></div>
                        </span>                        
                    </div>
                    <div className=".col-xs-6 .col-sm-3">
                        <div className="table-cell service-name-3">VIP</div>
                        <span>
                            <div className="table-cell">
                                <h5 className="service-school">UCB/</h5>
                            </div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell checked"></div>
                            <div className="table-cell checked"></div>
                        </span>                        
                    </div>
                </div>
            </div>
        );
    }

    renderEnrollNow(){
            return (
                <div className="take_actions">
                    <Particles/>
                    <div className="card">
                        <div className="title">
                            
                        </div>
                        <div className="action_button" onClick={this.openSalesModal.bind(this)}>
                            
                        </div>
                        <div className="description">
                            
                        </div>
                    </div>
                </div>
            )
        }

}

export default ShortTermIndexPage;


